import axios from "axios";

export const getNavigation = async version => {
  const response = await axios(`/docs/${version}/navigation.json`);
  return response.data;
};

export const getDocs = async (version, type, id) => {
  const response = await axios(`/docs/${version}/${type}/${id}/content.json`);
  return response.data;
};

export const getManifest = async version => {
  const response = await axios(`/docs/${version}/manifest.json`);
  return response.data;
};
