import React from "react";
import MainPageContainer from "./external/components/MainPage/MainPage.container";
import { Switch, Route } from "react-router-dom";

const DocsContainer = ({ versions }) => {
  const version = versions[0];

  return (
    <Switch>
      <Route
        exact
        path="/documentation/"
        render={props => <MainPageContainer version={version} {...props} />}
      />
      <Route
        exact
        path="/documentation/:type/:id"
        render={props => <MainPageContainer version={version} {...props} />}
      />
    </Switch>
  );
};

export default DocsContainer;
